export default function HomePage() {
  const pathname = usePathname();
  const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [isPinned, setIsPinned] = useState(true);
  const [isHovering, setIsHovering] = useState(false);
  const [isClient, setIsClient] = useState(false);
  useEffect(() => { setIsClient(true); }, []);
  
  const isDesktopExpanded = isPinned || isHovering;
  const shouldBeFullyOpen = isMobileMenuOpen || isDesktopExpanded;

  const renderNextLink = (href: string, children: React.ReactNode) => {
    return <Link href={href}>{children}</Link>;
  };
  
  const sidebarProps = {
    appName: "پنل مدیریت",
    logo: <FaShieldAlt />,
    menuItems: menuItems,
    footerItems: footerItems,
    currentPath: pathname,
    isExpanded: shouldBeFullyOpen,
    renderLink: renderNextLink,
  };

  const headerProps = {
    title: <Label text="صفحه اصلی" size="lg" />,
    showThemeSwitch: true,
    showNotificationButton: true,
    avatarDropdownItems: profileItems,
    userAvatar: {
        src: "https://i.pravatar.cc/150?u=raven",
        alt: "آواتار کاربر",
    },
    onMenuButtonClick: () => setMobileMenuOpen(true),
  };

  return (
    <div 
        onMouseEnter={() => isClient && window.innerWidth > 768 && setIsHovering(true)}
        onMouseLeave={() => isClient && window.innerWidth > 768 && setIsHovering(false)}
    >
        <AdminPageLayout
            sidebarProps={sidebarProps}
            headerProps={headerProps}
        >
            <h1 className="text-2xl font-semibold">داشبورد</h1>
            <p className="mt-2">به پنل مدیریت خوش آمدید.</p>
        </AdminPageLayout>
    </div>
  );
}